// Next.js - APICORS
import { NextResponse } from 'next/server'
import type { NextRequest } from 'next/server'

// 
const rateLimitMap = new Map<string, { count: number; timestamp: number }>()
const RATE_LIMIT_WINDOW = 60 * 1000 // 1
const RATE_LIMIT_MAX_REQUESTS = 100 // 100

// IP
const DEV_WHITELIST = ['127.0.0.1', '::1', 'localhost']

/**
 * 
 */
function checkRateLimit(ip: string): boolean {
  const now = Date.now()
  const key = `rate_limit_${ip}`
  
  const existing = rateLimitMap.get(key)
  
  if (!existing) {
    rateLimitMap.set(key, { count: 1, timestamp: now })
    return true
  }
  
  // 
  if (now - existing.timestamp > RATE_LIMIT_WINDOW) {
    rateLimitMap.set(key, { count: 1, timestamp: now })
    return true
  }
  
  // 
  if (existing.count >= RATE_LIMIT_MAX_REQUESTS) {
    return false
  }
  
  existing.count++
  return true
}

/**
 * 
 */
function cleanupRateLimit() {
  const now = Date.now()
  for (const [key, value] of rateLimitMap.entries()) {
    if (now - value.timestamp > RATE_LIMIT_WINDOW) {
      rateLimitMap.delete(key)
    }
  }
}

// 
setInterval(cleanupRateLimit, RATE_LIMIT_WINDOW)

/**
 * IP
 */
function getClientIP(request: NextRequest): string {
  const forwarded = request.headers.get('x-forwarded-for')
  const realIP = request.headers.get('x-real-ip')
  
  if (forwarded) {
    return forwarded.split(',')[0].trim()
  }
  
  if (realIP) {
    return realIP
  }
  
  return request.ip || '127.0.0.1'
}

/**
 * 
 */
export function middleware(request: NextRequest) {
  const { pathname } = request.nextUrl
  const clientIP = getClientIP(request)
  
  // API
  if (pathname.startsWith('/api/')) {
    const response = NextResponse.next()
    
    // CORS
    response.headers.set('Access-Control-Allow-Origin', '*')
    response.headers.set('Access-Control-Allow-Methods', 'GET, POST, PUT, DELETE, OPTIONS')
    response.headers.set('Access-Control-Allow-Headers', 'Content-Type, Authorization, X-Requested-With')
    response.headers.set('Access-Control-Max-Age', '86400')
    
    // 
    if (request.method === 'OPTIONS') {
      return new Response(null, { status: 200, headers: response.headers })
    }
    
    // 
    const isDev = process.env.NODE_ENV === 'development'
    if (isDev && DEV_WHITELIST.some(ip => clientIP.includes(ip))) {
      return response
    }
    
    // 
    if (!checkRateLimit(clientIP)) {
      return new NextResponse(
        JSON.stringify({
          success: false,
          error: 'Rate limit exceeded. Please try again later.',
          timestamp: new Date().toISOString()
        }),
        {
          status: 429,
          headers: {
            'Content-Type': 'application/json',
            'Retry-After': '60',
            ...Object.fromEntries(response.headers.entries())
          }
        }
      )
    }
    
    // 
    response.headers.set('X-Content-Type-Options', 'nosniff')
    response.headers.set('X-Frame-Options', 'DENY')
    response.headers.set('X-XSS-Protection', '1; mode=block')
    response.headers.set('Referrer-Policy', 'strict-origin-when-cross-origin')
    
    // API
    response.headers.set('X-API-Version', '1.0.0')
    
    // ID
    const requestId = `req_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`
    response.headers.set('X-Request-ID', requestId)
    
    return response
  }
  
  // API
  return NextResponse.next()
}

// 
export const config = {
  matcher: [
    // API
    '/api/:path*',
    // 
    '/((?!_next/static|_next/image|favicon.ico).*)',
  ],
}